import React from 'react'
import {Container, Row, Col, Button} from 'react-bootstrap'
import SocketContext from '../services/socket-context'

class Pong extends React.Component {

    constructor(props) {
        super(props);
        
        this.sendUp = this.sendUp.bind(this);
        this.sendDown = this.sendDown.bind(this);
    }

    componentDidMount() {}
    
    sendUp() {
        console.log("Ping!");
        this.context.emit('up', new Date());
    }

    sendDown() {
        console.log("Ping!");
        this.context.emit('down', new Date());
    }

    render() {
        return (
            <Container>
                <Row>
                    <Col>
                        <Button onClick={this.sendUp}> UP </Button>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <Button onClick={this.sendDown}> DOWN </Button>
                    </Col>
                </Row>
            </Container>
        );
    } 
}

Pong.contextType = SocketContext;

export default Pong;